body {
  background-image: url(2645e4d9a01c411e1d094ebdb0889189.png) 30 30 no-repeat;
  font-family: "Quicksand", sans-serif;
}

.colors {
  padding: 145px 191px 145px 150px;
}

.color {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  max-width: 270px;
}
.color:not(:last-child) {
  margin-bottom: 20px;
}

.color__shade {
  width: 70px;
  height: 70px;
  border-radius: 6px;
}
.color__shade_dark_shade100 {
  background: #1F2041;
}
.color__shade_dark_shade75 {
  background: rgba(31, 32, 65, 0.75);
}
.color__shade_dark_shade50 {
  background: rgba(31, 32, 65, 0.5);
}
.color__shade_dark_shade25 {
  background: rgba(31, 32, 65, 0.25);
}
.color__shade_dark_shade5 {
  background: rgba(31, 32, 65, 0.05);
}
.color__shade_purple {
  background: #BC9CFF;
}
.color__shade_green {
  background: #6FCF97;
}

.color__text {
  display: flex;
  flex-direction: column;
  width: 159px;
}

.color__caption {
  font-style: normal;
  font-weight: bold;
  font-size: 19px;
  line-height: 29px;
}

.color__value {
  font-style: normal;
  font-size: 19px;
}